import { useState } from "react";
import { useTranslation } from "react-i18next";
import InstituteStaff from "../../../UserStaffMember/InstituteStaff/InstituteStaff";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CustomAutomateSelect from "../../../Ui/Select/CustomAutomateSelect";
import StringLength from "../../../../Custom/Validation/StringLength";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import {
  useAutomateInstituteCreateDepartment,
  useAutomateInstitutePlateformList,
} from "../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import { useAddNewDepartment } from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
const CreateDepartment = ({ instituteId, onClose, onRefetch }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    dName: "",
    dTitle: "",
    sid: "",
    gr_initials: "",
    institute_type: "",
    institute_type_id: null,
    affiliation_with_institute: "",
    affiliation_with_institute_id: null,
    department_type: "",
    department_type_id: null,
    stream_type: "",
    stream_type_id: null,
  });
  const [staff, setStaff] = useState("");
  const [step, setStep] = useState(1);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [streamList, setStreamList] = useState([]);
  const { automateInstitutePlateformList } =
    useAutomateInstitutePlateformList();

  const [addDepartment] = useAddNewDepartment();
  const [automateInstituteCreateDepartment] =
    useAutomateInstituteCreateDepartment();

  const onIconClose = () => {
    if (step === 2) setStep(1);
    else onClose();
  };

  const onSelectInstituteType = (data) => {
    setState((prev) => ({
      ...prev,
      institute_type: data?.name,
      institute_type_id: data?._id,
    }));
    setStreamList(data?.streams);
  };
  const onSelectAffiliated = (data) => {
    setState((prev) => ({
      ...prev,
      affiliation_with_institute: data?.name,
      affiliation_with_institute_id: data?._id,
    }));
  };
  const onSelectDepartmentType = (data) => {
    setState((prev) => ({
      ...prev,
      department_type: data?.name,
      department_type_id: data?._id,
    }));
  };
  const onSelectStreamType = (data) => {
    setState((prev) => ({
      ...prev,
      stream_type: data?.name,
      stream_type_id: data?._id,
    }));
  };

  const onNext = () => {
    let validation = "";
    validation = nextValidation(state);
    if (existingRequiredField(validation)) {
      setStep(2);
    } else {
      setErrorField(validation);
    }
  };

  const onInputChange = (e, lnt) => {
    if (e.target.value <= lnt) {
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
    }
  };

  const onSelectStaff = (val) => {
    setStaff(val);
    setState((prev) => ({
      ...prev,
      sid: val?._id,
    }));
    setStep(2);
  };

  const onCreate = () => {
    let validation = requiredField(state, [
      "sid",
      "gr_initials",
      "institute_type",
      "institute_type_id",
      "affiliation_with_institute",
      "affiliation_with_institute_id",
      "department_type",
      "department_type_id",
      "stream_type",
      "stream_type_id",
    ]);
    if (existingRequiredField(validation) && instituteId) {
      setDisabled((pre) => !pre);
      if (state.affiliation_with_institute === "Anonymous") {
        addDepartment({
          departmentCreate: state,
          id: instituteId,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      } else {
        automateInstituteCreateDepartment({
          id: instituteId,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {step === 3 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(2)}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_new_dept")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onIconClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            {step === 1 ? (
              <UserMemberContentWrapper
                customStyle={{
                  paddingTop: "0",
                }}
              >
                <CustomAutomateSelect
                  selectLabel={t("type_of_institute")}
                  selectedValue={t("please_select_type_of_institute")}
                  onClick={onSelectInstituteType}
                  options={automateInstitutePlateformList?.institute_type ?? []}
                  defalutValue={state.institute_type}
                  errorShow={errorField["institute_type"]}
                />
                <CustomAutomateSelect
                  selectLabel={t("affiliated_with")}
                  selectedValue={t("please_select_affiliated_with")}
                  onClick={onSelectAffiliated}
                  options={
                    automateInstitutePlateformList?.affiliated_with?.length > 0
                      ? [
                          {
                            name: "Anonymous",
                          },
                          ...automateInstitutePlateformList?.affiliated_with,
                        ]
                      : [
                          {
                            name: "Anonymous",
                          },
                        ]
                  }
                  defalutValue={state.affiliation_with_institute}
                  errorShow={errorField["affiliation_with_institute"]}
                />
                <CustomAutomateSelect
                  selectLabel={t("department_type")}
                  selectedValue={t("please_select_department_type")}
                  onClick={onSelectDepartmentType}
                  options={
                    automateInstitutePlateformList?.department_type ?? []
                  }
                  defalutValue={state.department_type}
                  errorShow={errorField["department_type"]}
                />
                {state.department_type === "First Year" ? null : (
                  <CustomAutomateSelect
                    selectLabel={t("stream")}
                    selectedValue={t("please_select_stream")}
                    onClick={onSelectStreamType}
                    options={streamList ?? []}
                    defalutValue={state.stream_type}
                    errorShow={errorField["stream_type"]}
                  />
                )}

                <CreateButton label="next" onAction={onNext} />
              </UserMemberContentWrapper>
            ) : step === 2 ? (
              <UserMemberContentWrapper
                customStyle={{
                  paddingTop: "0",
                }}
              >
                <FormInput
                  label={t("department_name")}
                  placeholder={t("dept_example")}
                  name={"dName"}
                  value={state.dName}
                  type={"text"}
                  onChange={(e) => onInputChange(e, 45)}
                  errorShow={errorField["dName"]}
                />
                <StringLength maxLength={45} valueLength={state.dName} />

                <div
                  className={style.card_container_image}
                  onClick={() => setStep(3)}
                  style={{
                    cursor: "pointer",
                  }}
                >
                  <img
                    alt="not found"
                    src={
                      staff?.staffProfilePhoto
                        ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                  />
                  <div className={style.card_container_inner}>
                    {!staff?.staffFirstName ? (
                      <>
                        <h6>
                          {t("assign_dhead")} {t("optional")}
                        </h6>
                        <p>{t("search_from_joined_list")}</p>
                      </>
                    ) : (
                      <>
                        <h6>{`${staff?.staffFirstName ?? ""} 
                    ${staff?.staffMiddleName ?? ""}
                    ${staff?.staffLastName ?? ""}`}</h6>
                        <p>
                          {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                        </p>
                      </>
                    )}
                  </div>
                </div>

                <FormInput
                  label={t("give_dhead_title")}
                  placeholder={t("dhead_example")}
                  name={"dTitle"}
                  value={state.dTitle}
                  type={"text"}
                  onChange={(e) => onInputChange(e, 40)}
                  errorShow={errorField["dTitle"]}
                />
                <StringLength maxLength={40} valueLength={state.dTitle} />

                <FormInput
                  label={t("department_initials")}
                  placeholder={t("department_initials_placeholder")}
                  name={"gr_initials"}
                  value={state.gr_initials}
                  type={"text"}
                  onChange={(e) => onInputChange(e, 10)}
                />
                <StringLength maxLength={10} valueLength={state.gr_initials} />

                <CreateButton label="create" onAction={onCreate} />
              </UserMemberContentWrapper>
            ) : null}

            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default CreateDepartment;

const nextValidation = (state) => {
  let dt = !state.affiliation_with_institute
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : state.department_type === "First Year"
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : state.affiliation_with_institute === "Anonymous"
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type_id",
        "stream_type_id",
      ]);

  return dt;
};
